	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/scribbling-away-during-meetings-bf1c1a46f018/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/portfolio-2018/static/1*V8WgG38jMeJ2lITvnlLM5A-467de9127549f8ae92b6e553de5b1633-2f776.jpeg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 630px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.39999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"1 V8WgG38jMeJ2lITvnlLM5A\"\n        title=\"\"\n        src=\"/portfolio-2018/static/1*V8WgG38jMeJ2lITvnlLM5A-467de9127549f8ae92b6e553de5b1633-af57e.jpeg\"\n        srcset=\"/portfolio-2018/static/1*V8WgG38jMeJ2lITvnlLM5A-467de9127549f8ae92b6e553de5b1633-847f7.jpeg 158w,\n/portfolio-2018/static/1*V8WgG38jMeJ2lITvnlLM5A-467de9127549f8ae92b6e553de5b1633-93973.jpeg 315w,\n/portfolio-2018/static/1*V8WgG38jMeJ2lITvnlLM5A-467de9127549f8ae92b6e553de5b1633-af57e.jpeg 630w,\n/portfolio-2018/static/1*V8WgG38jMeJ2lITvnlLM5A-467de9127549f8ae92b6e553de5b1633-28cf9.jpeg 945w,\n/portfolio-2018/static/1*V8WgG38jMeJ2lITvnlLM5A-467de9127549f8ae92b6e553de5b1633-0d474.jpeg 1260w,\n/portfolio-2018/static/1*V8WgG38jMeJ2lITvnlLM5A-467de9127549f8ae92b6e553de5b1633-66f39.jpeg 1890w,\n/portfolio-2018/static/1*V8WgG38jMeJ2lITvnlLM5A-467de9127549f8ae92b6e553de5b1633-2f776.jpeg 2000w\"\n        sizes=\"(max-width: 630px) 100vw, 630px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p><a href=\"https://unsplash.com/search/sketch?photo=gcsNOsPEXfs\">Image from Unsplash / William Iven</a></p>\n<h1>Scribbling away during meetings.</h1>\n<p>I’ve become quite keen on making what’s known as “sketch notes” during meetings and presentations. It’s something that I first encountered a little while ago when I met people at conferences who were making visual notes, but it wasn’t until I really started needing to write extensive notes myself that I gave it a try.</p>\n<p>Sketch notes might seem like a slightly more jovial approach to note-taking — and some people will even think you’re simply not paying attention to proceedings and are just drawing doodles — but there’s a little more down-to-earth seriousness to the idea.</p>\n<p>Fundamentally we all absorb information in different ways, and we recall and learn in different fashions, and sketch notes tap into our acceptance of that fact. Linear, text-based notes don’t suit everyone and neither would a note-free environment. Some people write notes because they won’t remember things otherwise, and others write specifically because the act of writing them will help them remember.</p>\n<p><a href=\"http://www.teachthought.com/pedagogy/literacy/10-brilliant-examples-of-sketch-notes-notaking-for-the-21st-century/\">TeachThought has a really great article on sketch notes.</a> To pull a few key quotes from the article:</p>\n<blockquote>\n<p><em>Sketch notes–or graphic notes, or whatever other term you like–are one of the single most important developments in note-taking history.</em></p>\n</blockquote>\n<blockquote>\n<p><em>The point of notes […] is to capture important ideas for future reference. While it’s nothing new to take notes that combine images with words and phrases, sketch notes are actually an evolution of this idea.</em></p>\n</blockquote>\n<p>I would really like to produce beautiful sketches every time I go into a meeting, but it rarely happens. Most of my output is a mess of intersecting lines, repeated underlines/outlines and interconnected boxes, but in the end my notes are not meant to be beautiful — they’re done for my benefit, and because I find the output a useful tool for recapping a subject at a later date.</p>\n<p>And yeah, it’s kinda fun as well.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/portfolio-2018/static/0*1tCuLQx55I4c4HlB-492c379b42682fb514994a3ba7586f3f-6d3a3.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 630px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"0 1tCuLQx55I4c4HlB\"\n        title=\"\"\n        src=\"/portfolio-2018/static/0*1tCuLQx55I4c4HlB-492c379b42682fb514994a3ba7586f3f-af57e.jpg\"\n        srcset=\"/portfolio-2018/static/0*1tCuLQx55I4c4HlB-492c379b42682fb514994a3ba7586f3f-847f7.jpg 158w,\n/portfolio-2018/static/0*1tCuLQx55I4c4HlB-492c379b42682fb514994a3ba7586f3f-93973.jpg 315w,\n/portfolio-2018/static/0*1tCuLQx55I4c4HlB-492c379b42682fb514994a3ba7586f3f-af57e.jpg 630w,\n/portfolio-2018/static/0*1tCuLQx55I4c4HlB-492c379b42682fb514994a3ba7586f3f-28cf9.jpg 945w,\n/portfolio-2018/static/0*1tCuLQx55I4c4HlB-492c379b42682fb514994a3ba7586f3f-6d3a3.jpg 1080w\"\n        sizes=\"(max-width: 630px) 100vw, 630px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<hr>\n<p><em>Originally published at</em> <a href=\"https://www.psyked.co.uk/scribbling-away-during-meetings/\"><em>www.psyked.co.uk</em></a> <em>on January 24, 2017.</em></p>","frontmatter":{"title":"Scribbling away during meetings.","date":"January 24, 2017","tags":["Design","Productivity"],"url":"https://medium.com/@psyked/scribbling-away-during-meetings-bf1c1a46f018"}}},"pathContext":{}}

